import M3U8FileParser from "m3u8-file-parser";
import { useEffect, useState } from "react";
import { sortChannels } from "../utils/Helper";
import { useNavigate } from 'react-router-dom';
import '../utils/Styles.css'

function Tamil() {

    const navigate = useNavigate();

    const [allChannels, setAllChannels] = useState([]);
    const [filteredChannels, setFilteredChannels] = useState([]);
    const [query, setQuery] = useState("")

    useEffect(() => {
        getData();
    }, []);

    useEffect(() => {
        filterData();
    }, [allChannels,query]);

    async function getData() {
        try {
            const allResponses = await Promise.all([
                fetch('https://prasanth-snk.github.io/iptv/tamilLocal.m3u').then(resp => resp.text()),
                fetch('https://prasanth-snk.github.io/youtube_m3u/tamil.m3u').then(resp => resp.text()),
                fetch('https://iptv-org.github.io/iptv/languages/tam.m3u').then(resp => resp.text()),
                fetch('https://prasanth-snk.github.io/iptv/tamil_satellite_tv.m3u').then(resp => resp.text())
            ]);
            var reader = new M3U8FileParser();
            reader.read(allResponses[0]);
            reader.read(allResponses[1]);
            reader.read(allResponses[2]);
            reader.read(allResponses[3]);

            setAllChannels(sortChannels(reader.getResult()));
        } catch (err) {
            console.log(err)
        }
    }
    async function filterData() {
        if (query) {
            const filtered = allChannels.map((item) => item.inf.title.toLowerCase().includes(query.toLowerCase()));
            setFilteredChannels(filtered);
        } else {
            setFilteredChannels(allChannels);
        }
    }

    function handleClick(item) {
        navigate('/player', { state: { ...item } });
    }

    return (
        <div>
            {/* <input placeholder="Enter Post Title" onChange={event => setQuery(event.target.value)} /> */}
            <div className="Grid">
                {filteredChannels.map((item, index) => {
                    return (<div className="GridItem" key={index} onClick={() => handleClick(item)} >
                        <img className="ChannelLogo" src={item?.inf?.tvgLogo} alt="new" />
                        <div>{item.inf.title}</div>
                    </div>)
                })}
            </div>
        </div>
    );
}

export default Tamil;
